.about {
   padding: 200px 0;
   background-color: #000a17;
		// .about__content

		&__content {
      display: flex;
      align-items: center;
      gap: 70px;
		}

		// .about__info

		&__info {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 32px;
      max-width: 400px;
		}

		// .about__tag

		&__tag {
      font-size: 18px;
      padding: 12px 16px;
      background-color: white;
      border-radius: 20px;
		}

		// .about__title

		&__title {
      font-weight: 400;
      font-size: 52px;
      line-height: 1.07692;
      color: white;
      margin: 0;

      span{
        background: linear-gradient(45deg, #92aef2 0%, #517feb 100%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
		}

		// .about__text

		&__text {
      font-size: 14px;
      color: #fff;
      margin: 0;
		}

		// .about__link

		&__link {
      display: flex;
      align-items: center;
      gap: 10px;
      padding: 12px 80px;
      background-color: var(--accent-color);
      font-size: 16px;
      color: white;
      border-radius: 24px;
		}

		// .about__list

		&__list {
      display: grid;
      grid-template-columns: 1fr 1fr;
      align-items: flex-start;
      gap: 8px;
		}

		// .about__item

		&__item {
      position: relative;
      backdrop-filter: blur(120px);
      background: linear-gradient(137deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%);
      border-radius: 36px;
      padding: 20px;
      padding-right: 70px;
      overflow: hidden;
      &:nth-child(1){
        min-height: 340px;
      }
      &:nth-child(2){
        min-height: 280px;
        align-self: flex-end;
      }
      &:nth-child(3){
        min-height: 280px;
      }
      &:nth-child(4){
        min-height: 340px;
      }

      &--accent{
        box-shadow: 0 18px 88px -4px rgba(255, 255, 255, 0.14), 0 8px 28px -17px rgba(255, 255, 255, 0.12);
        background: #2e64e7;
      }
		}

		// .about__item-title

		&__item-title {
      font-size: 18px;
      color: white;
      margin-bottom: 12px;
      display: block;
		}

		// .about__item-text

		&__item-text {
      font-size: 14px;
      color: white;
		}

		// .about__item-arrow

		&__item-arrow {
      position: absolute;
      top: 20px;
      right: 20px;
		}

		// .about__item-bg

		&__item-bg {
      position: absolute;
      bottom: 0;
      right: 0;
		}
}

.work-steps {
    padding: 150px 0;
    border-radius: 64px;
    background-color: #0b131c;
		// .work-steps__head

		&__head {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 64px;
		}

		// .work-steps__title

		&__title {
      font-weight: 400;
      font-size: 52px;
      color: white;
      margin: 0;
      line-height: 56px;

      span{
        background: linear-gradient(45deg, #fff 0%, #a0b8f7 100%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
		}

		// .work-steps__text

		&__text {
      font-size: 14px;
      color: white;
      max-width: 356px;
		}

		// .work-steps__application

		&__application {
      display: flex;
      align-items: center;
      gap: 10px;
      padding: 12px 24px;
      border-radius: 24px;
      background-color: var(--accent-color);
      color: white;
      line-height: normal;
		}

		// .work-steps__steps

		&__steps {
      margin-bottom: 64px;
      display: grid;
      grid-template-columns: repeat(6, 1fr);

      svg{
        grid-column: 6 span;
      }
		}

		// .work-steps__info

		&__info {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
		}

		// .work-steps__info-item

		&__info-item {
      display: flex;
      flex-direction: column;
      gap: 12px;
      max-width: 180px;
      &--active{
        padding-left: 12px;


        .work-steps__info-title{
         color: white;
         &::after{
          opacity: 1;
         }
        }
        .work-steps__info-text{
          color: white;
        }
      }
		}

		// .work-steps__info-title

		&__info-title {
      font-weight: 500;
      font-size: 20px;
      color: #474d5a;
      line-height: 1.2;
      position: relative;
      transition: all .2s ease-in;
      &::after{
        display: block;
        content: "";
        position: absolute;
        left: -12px;
        bottom: 0;
        top: 0;
        width: 2px;
        background-color: #2e64e7;
        opacity: 0;
        transition: all .2s ease-in;
      }
		}

		// .work-steps__info-text

		&__info-text {
      font-weight: 400;
      font-size: 14px;
      color: #474d5a;
      line-height: 22px;
      transition: all .2s ease-in;
		}
}
