.accordion {
    display: flex;
    flex-direction: column;
    gap: 8px;
		// .accordion__item

		&__item {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      gap: 12px;
      backdrop-filter: blur(120px);
      background: linear-gradient(137deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%);
      border-radius: 36px;
      padding: 20px;
      transition: all .2s ease-in;
      &--active{
        background-color: var(--accent-color);
        box-shadow: 0 18px 88px -4px rgba(255, 255, 255, 0.14), 0 8px 28px -6px rgba(255, 255, 255, 0.12);
        height: 100%;
        .accordion__item-arrow{
          transform: rotate(45deg);
        }
        .accordion__item-text{
          opacity: 1;
        }
        .accordion__item-img{
          opacity: 1;
        }
      }

      svg{
        flex: none;
      }
		}

    &__item-inner{

    }

		// .accordion__item-info

		&__item-info {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      text-align: left;
      gap: 12px;
      margin-top: 15px;
      max-width: 470px;
		}

		// .accordion__item-title

		&__item-title {
      font-weight: 400;
      font-size: 18px;
      color: white;
		}

		// .accordion__item-text

		&__item-text {
      margin: 0;
      font-weight: 400;
      font-size: 14px;
      color: white;
      line-height: 20px;
      opacity: 0;
      transition: all .2s ease-in;
		}

		// .accordion__item-img

		&__item-img {
      opacity: 0;
      transition: all .2s ease-in;
		}

    &__item-arrow{
      margin-left: 12px;
    }
}

